.dialogs {
display: grid;
grid-template-columns: 2fr 10fr;

}

.dialogList {
padding:10px;
}
.dialogList .active {
color:rgb(5, 81, 83);
}
.messages {
padding:10px;
}

.name a {
    text-decoration: none;
    
}

.button { 
    box-shadow: rgb(39 104 115) 0px 10px 14px -7px;
    background-color: rgb(150, 155, 156);
    border-radius: 8px;
    display: inline-block;
    cursor: pointer;
}

.textArea {
    background-image:url('data:image/jpeg;base64,');
	background-repeat:repeat-x;
	border:1px solid #d1c7ac;
	width: 230px;
	height: 60px;
	color:#333333;
	padding:3px;
	margin-right:4px;
	margin-bottom:8px;
	font-family:tahoma, arial, sans-serif;
}